@import '../../../assets/css/mixins';
@import '../../../assets/css/theme';
.dao-governance-active-empty {
  padding: 65px;
  background: $color-dao-proposals-card-bg-default;
  text-align: center;
  border-radius: 5px;
  img {
    display: block;
    margin: 0 auto;
    width: 123px;
  }
  &-text {
    margin: 22px 0 40px;
    font-family: PingFangSC, PingFang SC;
    font-weight: 400;
    font-size: 12px;
    color: $color-text-default-5;
    line-height: 17px;
  }
  &-btn {
    display: inline-block;
    padding: 7px 20px;
    font-family: PingFangSC, PingFang SC;
    font-weight: 600;
    font-size: 16px;
    background: $color-button-primary;
    color: #ffffff;
    line-height: 22px;
    border-radius: 36px;
    cursor: pointer;
    &:hover {
      background: #a87bff;
    }
  }
}

@include mobile-only {
  .dao-governance-active-empty {
    padding: 30px 30px 30px;
    border-radius: 10px;
    background: $color-farming-card-bg;
    &-text {
      margin: 17px 0 26px;
    }
    &-btn {
      padding: 7px 24px;
      font-size: 14px;
      line-height: 20px;
    }
  }
}
