@import '../../../assets/css/mixins';

.dao-governance-index-banner {
  position: relative;
  &-bg {
    width: 100%;
    img {
      display: block;
      width: 100%;
    }
  }
  &-text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    img {
      display: block;
      height: 100px;
    }
  }
}

@include mobile-only {
  .dao-governance-index-banner {
    &-text {
      img {
        height: 40px;
      }
    }
  }
}
