@import '../../../assets/css/mixins';
@import '../../../assets/css/theme';
.dao-governance-index-proposals-wrap {
  .dao-governance-index-proposals-list {
    border-radius: 5px;
    overflow: hidden;
    &-row {
      display: none;
    }
    &-mobile {
      display: none;
    }
    .dao-governance-index-proposals-item {
      position: relative;
      padding: 20px 130px 20px 20px;
      background: $color-dao-proposals-card-bg-default;
      cursor: pointer;
      &:hover {
        background: $color-dao-proposals-card-bg-active;
        @include mobile-only {
          background: $color-dao-proposals-card-bg-default;
        }
      }
      &::after {
        content: '';
        position: absolute;
        left: 20px;
        right: 20px;
        bottom: 0;
        height: 1px;
        background: #534c69;
        @include lightThemeOnly {
          background: #ddd;
        }
      }
      &:last-child {
        &::after {
          display: none;
        }
      }
      .dao-governance-index-proposals-list-main {
        font-family: PingFangSC, PingFang SC;
        font-weight: 600;
        font-size: 16px;
        color: $color-text-default;
        line-height: 22px;
        display: flex;
        align-items: flex-start;
        .dao-governance-index-proposals-list-main-l {
          margin-right: 8px;
          .dao-governance-index-proposals-list-num {
            position: relative;
            top: -1px;
          }
        }
        .dao-governance-index-proposals-list-main-c {
          flex: 1;
          .dao-governance-index-proposals-list-name {
            min-height: 22px;
          }
        }
      }
      .dao-governance-index-proposals-list-num {
        padding: 0 4px;
        font-family: PingFangSC, PingFang SC;
        font-weight: 600;
        font-size: 12px;
        line-height: 15px;
        color: $color-text-default-7;
        background: $color-text-default-1;
        border-radius: 2px;
        vertical-align: middle;
      }
      .dao-governance-index-proposals-list-desc {
        margin-top: 16px;
        font-family: PingFangSC, PingFang SC;
        font-weight: 400;
        font-size: 12px;
        color: $color-text-default-5;
        line-height: 17px;
      }
      .dao-governance-index-proposals-list-status {
        position: absolute;
        right: 20px;
        top: 20px;
      }
    }
    &.dao-governance-index-proposals-list-flag {
      background: transparent;
      .dao-governance-index-proposals-item {
        padding: 50px 130px 20px 20px;
        margin-bottom: 10px;
        border-radius: 5px;
        &:last-child {
          margin-bottom: 0;
        }
        &::after {
          display: none;
        }
      }
      .dao-governance-index-proposals-flag {
        display: block;
      }
      .dao-governance-index-proposals-list-status {
        right: 20px;
        top: 50px;
      }
    }
  }
  .dao-governance-index-proposals-flag {
    display: none;
    position: absolute;
    left: 0;
    top: 0;
    padding: 5px 10px;
    font-family: PingFangSC, PingFang SC;
    font-weight: 600;
    font-size: 14px;
    color: #ffffff;
    line-height: 20px;
    background: linear-gradient(137deg, #ffa700 0%, #ff8700 100%);
    border-radius: 5px 0px 12px 0px;
  }
}

@include mobile-only {
  .dao-governance-index-proposals-wrap {
    .dao-governance-index-proposals-list {
      border-radius: 5px;
      &-mobile {
        display: block;
      }
      &-pc {
        display: none;
      }
      .dao-governance-index-proposals-item {
        padding: 10px 10px 16px 10px;
        &::after {
          left: 10px;
          right: 10px;
        }
        .dao-governance-index-proposals-list-main {
          .dao-governance-index-proposals-list-main-l {
            display: none;
          }
        }
        .dao-governance-index-proposals-list-num {
          position: absolute;
          right: 0;
          top: 3px;
          padding: 0 2px;
          line-height: 17px;
          color: $color-text-default;
        }
        .dao-governance-index-proposals-list-desc {
          margin-top: 12px;
        }
        .dao-governance-index-proposals-list-status {
          position: relative;
          right: 0;
          top: 0;
          margin-bottom: 10px;
        }
      }
      &.dao-governance-index-proposals-list-flag {
        background: $color-farming-card-bg;
        .dao-governance-index-proposals-list-row {
          display: flex;
          align-items: center;
          margin-top: 12px;
          & > :first-child {
            margin-left: 0 !important;
          }
          .dao-governance-index-proposals-list-num {
            position: relative;
            right: 0;
            top: 0;
            display: inline-block;
            margin-left: 14px;
          }
        }
        .dao-governance-index-proposals-item {
          padding: 35px 10px 16px 10px;
          &:last-child {
            margin-bottom: 0;
          }
          &::after {
            display: none;
          }
          .dao-governance-index-proposals-list-status {
            display: none;
          }
        }
        .dao-governance-index-proposals-list-main {
          .dao-governance-index-proposals-list-num {
            display: none;
          }
        }
      }
    }
    .dao-governance-index-proposals-flag {
      padding: 4px 6px;
      font-size: 12px;
      line-height: 17px;
    }
  }
}
