@import '../../../assets/css/mixins';
@import '../../../assets/css/theme';

.dao-governance-index {
  .dao-governance-layout {
    margin: 0 auto 0;
    width: 1200px;
  }
  .dao-governance-banner-wrap {
    position: relative;
    // z-index: -1;
    margin-top: -72px;
  }
  .dao-governance-main {
    margin-top: 30px;
    padding: 26px 30px 30px;
    background: $color-farming-card-bg;
    border-radius: 10px;
    &-wrap {
      display: flex;
    }
    &-top {
      position: relative;
      padding-bottom: 20px;
      &-tip {
        position: absolute;
        top: 9px;
        left: 0;
        width: 100%;
        text-align: center;

        &-inner {
          display: inline-flex;
          align-items: center;
          justify-content: center;
          &:hover {
            img {
              opacity: 1;
            }
            span {
              color: $color-a-hover;
            }
          }
        }
        &-ico {
          display: block;
          margin-right: 4px;
          width: 12px;
          height: 12px;
          opacity: 0.7;
          cursor: pointer;
        }
        span {
          margin-right: 4px;
          font-family: PingFangSC, PingFang SC;
          font-weight: 400;
          font-size: 12px;
          color: $color-a;
          line-height: 17px;
          cursor: pointer;
        }
        &-link {
          display: block;
          width: 8px;
          height: 8px;
          cursor: pointer;
          opacity: 0.7;
        }
      }
    }
    &-title {
      display: inline-flex;
      align-items: center;
      &-label {
        margin-right: 9px;
        font-family: PingFangSC, PingFang SC;
        font-weight: 600;
        font-size: 20px;
        color: $color-text-default;
        line-height: 28px;
      }
      &-num {
        padding: 0 6px;
        background: rgba(193, 160, 255, 0.3);
        border-radius: 2px;
        font-family: PingFangSC, PingFang SC;
        font-weight: 600;
        font-size: 14px;
        color: $color-a;
        line-height: 18px;
      }
    }
    &-previous {
      padding: 20px 0;
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      .dao-governance-main-previous-ico-active {
        display: none;
      }
      &-active {
        img {
          transform: rotateZ(180deg);
        }
        span {
          color: $color-a-hover !important;
        }
        .dao-governance-main-previous-ico {
          display: none;
        }
        .dao-governance-main-previous-ico-active {
          display: block;
        }
        .dao-governance-main-previous-ico-active-light {
          display: none;
        }
        @include lightThemeOnly {
          .dao-governance-main-previous-ico-active-dark {
            display: none;
          }
          .dao-governance-main-previous-ico-active-light {
            display: block;
          }
        }
      }
      span {
        display: inline-flex;
        margin-right: 7px;
        font-family: PingFangSC, PingFang SC;
        font-weight: 600;
        font-size: 14px;
        color: $color-a;
        line-height: 20px;
        cursor: pointer;
        @include desktop-only {
          &:hover {
            color: $color-a-hover !important;
          }
        }
      }
      .dao-governance-main-previous-ico-wrap {
        width: 10px;
        height: 10px;
        cursor: pointer;
        @include desktop-only {
          &:hover {
            .dao-governance-main-previous-ico {
              display: none;
            }
            .dao-governance-main-previous-ico-active-dark {
              display: block;
            }
            @include lightThemeOnly {
              .dao-governance-main-previous-ico-active-dark {
                display: none;
              }
              .dao-governance-main-previous-ico-active-light {
                display: block;
              }
            }
          }
        }
      }
      img {
        display: block;
        width: 100%;
        height: 100%;
      }
    }
    &-l {
      flex: 1;
      min-height: 356px;
      &-holder {
        min-height: 356px;
      }
    }
    &-r {
      margin-left: 30px;
      width: 400px;
    }
    &-faq {
      margin-top: 20px;
    }
    :global(.ant-spin-container) {
      &::after {
        display: none !important;
      }
    }
  }
}

@include min-desktop-only {
  .dao-governance-index {
    .dao-governance-layout {
      width: 100%;
      padding: 0 20px;
    }
  }
}

@include mobile-only {
  .dao-governance-index {
    .dao-governance-layout {
      padding: 0 15px;
      width: 100%;
    }
    .dao-governance-banner-wrap {
      margin-top: 0;
    }
    .dao-governance-main {
      padding: 0;
      background: transparent;
      border-radius: 0;
      &-wrap {
        display: block;
      }
      &-l {
        border-radius: 10px;
        background: $color-farming-card-bg;
        min-height: 200px;
        &-holder {
          min-height: 200px;
        }
      }
      &-list-wrap {
        padding: 10px;
      }
      .dao-governance-main-list-active-wrap {
        margin-bottom: 16px;
      }
      &-top {
        position: relative;
        padding-bottom: 12px;
        &-tip {
          margin-top: 12px;
          position: relative;
          top: 0;
          width: 100%;
          text-align: left;
        }
      }
      &-title {
        display: inline-flex;
        align-items: center;
        &-label {
          margin-right: 9px;
          font-size: 16px;
          line-height: 22px;
        }
        &-num {
          font-size: 12px;
          line-height: 17px;
        }
      }
      &-previous {
        // padding: 16px 0;
        margin-bottom: 16px;
        padding: 0;
        span {
          margin-right: 6px;
        }
      }
      &-l {
        flex: initial;
      }
      &-r {
        margin-left: 0;
        width: 100%;
      }
      &-forum {
        margin-top: 20px;
      }
    }
  }
}
