@import '../../../assets/css/mixins';
@import '../../../assets/css/theme';

// Active 0、1
$-color-status-active: rgba(255, 172, 0, 1);
$-bg-color-status-active: rgba(255, 172, 0, 0.1);
$-bg-color-status-light-active: rgba(255, 172, 0, 0.1);

// Passed 4、5
$-color-status-passed: rgba(120, 255, 199, 1);
$-color-status-light-passed: rgba(1, 166, 146, 1);
$-bg-color-status-passed: rgba(120, 255, 199, 0.1);
$-bg-color-status-light-passed: rgba(230, 246, 244, 1);

// Executd 7
$-color-status-executed: rgba(193, 160, 255, 1);
$-color-status-light-executed: rgba(114, 47, 255, 1);
$-bg-color-status-executed: rgba(193, 160, 255, 0.1);
$-bg-color-status-light-executed: rgba(241, 234, 255, 1);

// Failed 2、3、6
$-color-status-failed: rgba(255, 73, 106, 1);
$-bg-color-status-failed: rgba(255, 73, 106, 0.1);
$-bg-color-status-light-failed: rgba(255, 73, 106, 0.1);

.dao-governance-status-tag {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0 5px;
  min-width: 104px;
  font-family: PingFangSC, PingFang SC;
  font-weight: 600;
  font-size: 12px;
  color: $-color-status-active;
  font-style: normal;
  line-height: 22px;
  height: 24px;
  background: $-bg-color-status-active;
  border-radius: 5px 5px 5px 1px;
  border: 1px solid $-color-status-active;
  white-space: nowrap;
  &-point {
    display: none;
    margin: 0 10px;
    width: 6px;
    height: 6px;
    background: $-color-status-active;
    border-radius: 6px;
  }
  @include mobile-only {
    min-width: 80px;
    line-height: 20px;
    height: 22px;
  }
}
.dao-governance-status-tag-active {
  border-color: $-color-status-active;
  color: $-color-status-active;
  background-color: $-bg-color-status-active;
  @include lightThemeOnly {
    background-color: $-bg-color-status-light-active;
  }
  .dao-governance-status-tag-point {
    background-color: $-color-status-active;
  }
}
.dao-governance-status-tag-passed {
  border-color: $-color-status-passed;
  color: $-color-status-passed;
  background-color: $-bg-color-status-passed;
  @include lightThemeOnly {
    border-color: $-color-status-light-passed;
    color: $-color-status-light-passed;
    background-color: $-bg-color-status-light-passed;
  }
  .dao-governance-status-tag-point {
    background-color: $-color-status-passed;
    @include lightThemeOnly {
      background-color: $-color-status-light-passed;
    }
  }
}

.dao-governance-status-tag-executed {
  border-color: $-color-status-executed;
  color: $-color-status-executed;
  background-color: $-bg-color-status-executed;
  @include lightThemeOnly {
    border-color: $-color-status-light-executed;
    color: $-color-status-light-executed;
    background-color: $-bg-color-status-light-executed;
  }
  .dao-governance-status-tag-point {
    background-color: $-color-status-executed;
    @include lightThemeOnly {
      background-color: $-color-status-light-executed;
    }
  }
}

.dao-governance-status-tag-failed {
  border-color: $-color-status-failed;
  color: $-color-status-failed;
  background-color: $-bg-color-status-failed;
  @include lightThemeOnly {
    background-color: $-bg-color-status-light-failed;
  }
  .dao-governance-status-tag-point {
    background-color: $-color-status-failed;
  }
}
