@import '../../../assets/css/mixins';
@import '../../../assets/css/theme';
.dao-governance-forum {
  background: $color-dao-proposals-card-bg-default;
  border-radius: 10px;
  background-position: bottom right;
  background-repeat: no-repeat;
  backdrop-filter: 0.1;
  padding: 26px 30px 26px;
  &-title {
    position: relative;
    font-family: PingFangSC, PingFang SC;
    font-weight: 600;
    font-size: 20px;
    color: $color-text-default;
    line-height: 28px;
  }
  &-title-btn {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    font-family: PingFangSC, PingFang SC;
    font-weight: 400;
    font-size: 16px;
    color: $color-a;
    line-height: 22px;
    cursor: pointer;
    &:hover {
      color: $color-a-hover;
    }
  }
  &-card {
    margin-top: 20px;
    li {
      margin-bottom: 20px;
      opacity: 0.7;
      line-height: 20px !important;
      &:last-child {
        margin-bottom: 0;
      }
      span {
        margin-right: 8px;
        color: $color-text-default;
        cursor: pointer;
      }
      img {
        width: 8px;
        height: 8px;
      }
      &:hover {
        opacity: 1;
      }
    }
  }
}

@include mobile-only {
  .dao-governance-forum {
    padding: 20px 20px 20px 20px;
    background-color: $color-farming-card-bg;
    &-title {
      font-size: 16px;
      line-height: 22px;
    }
    &-title-btn {
      font-size: 14px;
      line-height: 20px;
    }
    &-card {
      margin-top: 16px;
      li {
        margin-bottom: 16px;
      }
    }
  }
}
