@import '../../../assets/css/mixins';
@import '../../../assets/css/theme';
.dao-governance-index-process {
  margin-top: 20px;
  padding: 20px;
  background: $color-farming-card-bg;
  border-radius: 10px;
  &-top {
    display: none;
  }
  &-list {
    display: flex;
  }
  &-item {
    position: relative;
    margin-right: 10px;
    flex: 1;
    padding: 20px 16px;
    background: rgba(255, 255, 255, 0.06);
    border-radius: 5px;
    border: 1px solid $color-text-default-3;
    font-family: PingFangSC, PingFang SC;
    @include lightThemeOnly {
      border-color: #e5e5e5;
    }
    &:last-child {
      margin-right: 0;
    }
  }
  &-name {
    display: flex;
    align-items: center;
    &-num {
      margin-right: 10px;
      width: 20px;
      height: 20px;
      img {
        display: block;
        width: 100%;
        height: 100%;
      }
    }
    &-label {
      font-weight: 600;
      font-size: 16px;
      color: $color-text-default;
      line-height: 22px;
    }
  }
  &-desc {
    margin-top: 13px;
    font-weight: 400;
    font-size: 12px;
    color: $color-text-default-7;
    line-height: 17px;
  }
  &-icon {
    position: absolute;
    right: 10px;
    top: 8px;
    width: 46px;
    height: 46px;
    img {
      display: block;
      width: 100%;
      height: 100%;
    }
  }
}

@include mobile-only {
  .dao-governance-index-process {
    margin-top: 10px;
    padding: 8px 12px;
    border-radius: 10px;
    &-active {
      .dao-governance-index-process-top {
        img {
          transform: rotateZ(180deg);
        }
      }
      .dao-governance-index-process-list {
        display: block;
      }
    }
    &-top {
      display: flex;
      align-items: center;
      justify-content: space-between;
      font-family: PingFangSC, PingFang SC;
      font-weight: 600;
      font-size: 16px;
      color: $color-text-default;
      line-height: 22px;
      img {
        display: block;
        width: 10px;
        height: 10px;
      }
    }
    &-list {
      display: none;
      margin-top: 16px;
      padding-bottom: 8px;
    }
    &-item {
      margin-right: 0;
      flex: initial;
      padding: 0 0 30px 0;
      background: transparent;
      border-radius: 0;
      border: 0;
      &:last-child {
        padding-bottom: 0;
        &::after {
          display: none;
        }
      }
      &::after {
        content: '';
        position: absolute;
        left: 10px;
        top: 0;
        height: 100%;
        width: 1px;
        border-left: 1px dashed rgba(193, 160, 255, 1);
      }
    }
    &-name {
      z-index: 2;
      position: relative;
      display: flex;
      align-items: center;
      &-label {
        font-size: 14px;
        line-height: 20px;
      }
    }
    &-desc {
      padding-left: 30px;
      margin-top: 10px;
    }
    &-icon {
      display: none;
    }
  }
}
